/*Commom_(start)*/
.color-one {
	color: grey;
}
.color-two {
	color: red;
}
.color-three {
	color: lightgrey;
}
button[type="button"] {
	padding: 5px 10px;
}
/*Commom_(end)*/

#post {
	background-color: lightgreen;
	-webkit-box-shadow: 4px 4px 4px 4px grey;
	        box-shadow: 4px 4px 4px 4px grey;
	width: 500px;
	height: 400px;
	margin: 50px auto;
	padding: 20px;
}

.title {
	text-align: center;
	border-bottom: 1px solid grey;
	margin-top: 0px;
}

.wrapper {
	background-color: white;
	border: 5px solid green;
	border-radius: 4px;
	width: 100%;
	max-height: 300px;
	overflow: auto;
}

.wrapper-image {
	width: 50%;
	float: left;
}

#newMessage {
	float: right;
	margin-top: 10px;
	margin-right: -10px;
}

/*CONTENT*/
.wrapper-content {
	display: inline-block;
	width: 45%;
	overflow: auto;
	margin-left: 2%;
	margin-top: 10px;
}
.wrapper-content-listItems {
	list-style-type: none;
}
.wrapper-content-listItems-item {
	border-bottom: 1px solid grey;
}
.wrapper-content-listItems>li:last-child {
	border-bottom: none;
}

/*NEW MESSAGE FORM*/
#stick {
	display: none;
	/*display: block;*/
	position: fixed;
	background-color: blue;
	padding: 10px;
	top: 100px;
	left: 50%;
	-webkit-box-shadow: 4px 4px 4px 4px grey;
	        box-shadow: 4px 4px 4px 4px grey;
}
#stick>input {
	padding: 5px 10px;
	width: 200px;	
	text-align: center;
}
#stick>input[value="Submit"] {
	display: block;
	margin: 10px auto;
}


